.App {
  font-family: sans-serif;
  text-align: center;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;

  content: "";

  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;

  border: 1px solid #999;

  float: left;

  font-size: 24px;

  font-weight: bold;

  line-height: 34px;

  height: 34px;

  margin-right: -1px;

  margin-top: -1px;

  padding: 0;

  text-align: center;

  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;

  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

#a {
  display: flex;

  width: 1300px;

  margin: 20px auto;

  padding: 50px 50px;

  flex-wrap: wrap;

  justify-content: center;

  background-color: Brown;
}

.buttom5 {
  position: absolute;

  /*position: sticky;   

  

   

  

